import ClickOutside from "vue-click-outside";

export default {
  props: ['offset'],
  data: () => ({
    defaultItem: "RU",
    itemsLang: ["RU", "KZ", "EN"],
    openLang: false,
    menu: false,
    weather_temp: null,
    weather_count: null,
  }),
  created() {
    this.loadTemp();
  },
  computed: {
    items() {
      return [
        {
          title: this.$t("projects"),
          url: "/Projects",
        },
        {
          title: this.$t("scheme"),
          url: "/InteractiveDiagram",
        },
        {
          title: this.$t("faq"),
          url: "/Faq",
        },
        {
          title: this.$t("compliance"),
          url: "/Compliance",
        },
        {
          title: this.$t("supply"),
          url: "/Supply",
        },
        {
          title: this.$t("managment"),
          url: "/Managment",
        },
        {
          title: this.$t("vacancy"),
          url: "/Vacancy",
        },
      ];
    },
  },
  methods: {
    goToInner() {
      this.menu = false;
      this.$emit("showMenu", this.menu);
    },
    goToContacts() {
      this.menu = false;
      this.$emit("showMenu", this.menu);
      this.$router.push(
        this.localePath({
          path: "/Contacts",
        })
      );
    },
    goToAbout() {
      this.menu = false;
      this.$emit("showMenu", this.menu);
      this.$router.push(
        this.localePath({
          path: "/About",
        })
      );
    },
    goToNews() {
      this.menu = false;
      this.$emit("showMenu", this.menu);
      this.$router.push(
        this.localePath({
          path: "/News",
        })
      );
    },
    onClickOutside() {
      this.openLang = false;
    },
    loadTemp() {
      this.$axios
        .get(`parts`)
        .then((res) => {
          this.weather_temp = JSON.parse(
            res.data.data.weather.value
          ).temperature;
          this.weather_count = this.weather_temp.toFixed();
        })
        .catch((e) => {
          console.log(e);
        });
    },
    showMenu() {
      this.menu = true;
      this.$emit("showMenu", this.menu);
    },
    closeMenu() {
      this.menu = false;
      this.$emit("showMenu", false);
      this.$store.commit("setVisibility", false);
    },
    selectLang(lang) {
      if (this.defaultItem == lang) return;
      this.openLang = false;
      this.defaultItem = lang;
      lang = lang.toLowerCase();
      if (this.$route.path != this.switchLocalePath(lang)) {
        this.$router.replace(this.switchLocalePath(lang));
      }
    },
    local() {
      let lang = this.$i18n.locale.toUpperCase();
      if (lang != this.defaultItem) {
        this.defaultItem = lang;
        this.selectLang(this.defaultItem);
      }
    },
    OutsideClick() {
      this.menu = false;
      this.$emit("showMenu", false);
    },
  },
  directives: {
    ClickOutside,
  },
};
