export default {
  data: () => ({
    text: null,
    ready: false,
  }),
  created() {
    this.interactiveBg();
  },
  methods: {
    async interactiveBg() {
      this.ready = false;
      const { data } = (await this.$axios.get(`parts`)).data;
      this.text = data.backgrounds.find(
        (el) => el.key == "interactive-diagram"
      );
      this.ready = true;
    },
  },
};
