export default {
  data: () => ({
    text: null,
    ready: false,
  }),
  created() {
    this.faqBg();
  },
  methods: {
    async faqBg() {
      this.ready = false;
      const { data } = (await this.$axios.get(`parts`)).data;
      this.text = data.backgrounds.find((el) => el.key == "compliance");
      this.ready = true;
    },
  },
};
